.contacts__list {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .contacts__list {
    grid-template-columns: 1fr;
  }
}
.contacts__list::before {
  position: absolute;
  content: "";
  top: 15px;
  bottom: 15px;
  left: 50%;
  border-left: 1px solid #74819c;
}
@media (max-width: 767px) {
  .contacts__list::before {
    display: none;
  }
}

.contacts__item {
  padding: 15px 60px 9px;
}
@media (max-width: 1199px) {
  .contacts__item {
    padding: 19px 48px 9px;
  }
}
@media (max-width: 767px) {
  .contacts__item {
    padding: 19px 0 6px;
  }
}
@media (max-width: 767px) {
  .contacts__item--order {
    grid-row: 2/3;
  }
}
.contacts__item h3 {
  margin: 0;
  margin-bottom: 9px;
  font-size: 26px;
  line-height: 100%;
  font-weight: 700;
  font-size: 16px;
}
@media (max-width: 1199px) {
  .contacts__item h3 {
    margin-bottom: 11px;
  }
}
@media (max-width: 767px) {
  .contacts__item h3 {
    margin-bottom: 13px;
    font-size: 22px;
  }
}
@media (max-width: 1199px) {
  .contacts__item h3 {
    font-size: 20px;
  }
}
@media (max-width: 767px) {
  .contacts__item h3 {
    font-size: 20px;
  }
}
.contacts__item p,
.contacts__item a {
  margin: 0;
  font-size: 14px;
  line-height: 1.3;
}
@media (max-width: 1199px) {
  .contacts__item p,
.contacts__item a {
    font-size: 18px;
  }
}
.contacts__item a:hover {
  opacity: 0.7;
}
.contacts__item a:active {
  opacity: 0.5;
}

.main-footer {
  display: grid;
  grid-template-columns: 170px auto 170px;
  padding: 41px var(--side-offset);
  background-color: #030a1a;
}
@media (max-width: 1199px) {
  .main-footer {
    grid-template-columns: 1fr 4.8fr 1fr;
    padding: 43px calc(var(--side-offset) - 6px) 43px var(--side-offset);
  }
